
:root{
  --lightgray: #0b242f;
  --gray: #040506;
  --orange: #1292e2;
  --darkGrey: #081b2b;
  --caloryCard: #190a3d;
  --planCard: linear-gradient(210.41deg, #4842fa 1.14%, #1bf0e9 100.75%);
  --planerCard: linear-gradient(210.41deg, #fa425e 1.14%, #1bf0e9 100.75%);
  --appColor: #072258;
}

.App {
  background-image: url(data:image/png;base64,);
  mix-blend-mode: overlay;
  background-color: var(--caloryCard);
  display: flex;
  flex-direction: column;
  gap: 6rem;
  overflow: hidden;
}
::-webkit-scrollbar{
  display: none;
}

.stroke-text{
  color: transparent;
  font-family:Arial, Helvetica, sans-serif;
  -webkit-text-stroke-width: 4px;
  -webkit-text-stroke-color: white;
}
.justify-the-text{
  text-align: justify;
  text-justify: inter-word;
}

.btn{
  background-color: white;
  padding: 0.5rem;
  font-weight: bold;
  border: 4px solid transparent;
  transition: 300ms all;
  display: flex;
  align-items: center;
  justify-content: center;
}

.btn:hover{
  cursor: pointer;
}

.blur{
  background: rgba(43, 193, 253, 0.69);
  position: absolute;
  border-radius: 50%;
  filter: blur(150px);
  z-index: -9;

}